import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import PropTypes from 'prop-types';
import {Consumer} from '../../context';
import axios from 'axios';

class Contact extends Component {
    state = {
        showContactInfo: false 
    };

    onShowClick = (e) => {
        this.setState({ showContactInfo: !this.state.showContactInfo });
    };

    onDeleteClick = async (id, dispatch) => {
        try {
            await axios.delete(`https://jsonplaceholder.typicode.com/users/${id}`); 
            dispatch({type: 'DELETE', payload: id});  
        } catch (error) {
            dispatch({type: 'DELETE', payload: id});
        }
    };

    render() {
        const {id, name, email, phone} = this.props.contact; 
        const {showContactInfo} = this.state;

        return (
            <Consumer>
                {value => {
                    const {dispatch} = value;
                    return (
                        <div className="card card-body mb-3">
                            <h4>{name} 
                                <i onClick={this.onShowClick} className="fa fa-sort-down" style={{cursor: 'pointer'}}></i>
                                <i className="fa fa-times" style={{cursor: 'pointer', float: 'right', color: 'red'}} onClick={this.onDeleteClick.bind(this, id, dispatch)}></i>

                                <Link to={`contacts/edit/${id}`}>
                                    <i 
                                        className="fa fa-pencil"
                                        style={{cursor: 'pointer', float: 'right', color: 'black', marginRight: '1rem'}}
                                    ></i>
                                </Link>
                            </h4>
                            {showContactInfo ? (<ul className="list-group">
                                <li className="list-group-item">Email: {email}</li>
                                <li className="list-group-item">Phone: {phone}</li>
                            </ul>) : null}
                        </div>
                    )
                }}
            </Consumer>
        )
    }
}

Contact.propTypes = {
    contact: PropTypes.object.isRequired
};

export default Contact;